/* styles for tooltip component */

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 3px dotted rgb(134, 133, 133);
    margin-top: 25px;
}


/* for tooltip text */

.tooltiptext {
    width: 300px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 12px;
    padding: 14px 12px;
    position: absolute;
    z-index: 1;
    font-size: 16px;
    transition: visibility 0.2s;
}


/*Here we are styling for class as top  */

.top {
    bottom: 100%;
    left: 50%;
    margin-bottom: 10px;
    margin-left: -100px;
}


/* Here we are styling for class as bottom */

.bottom {
    top: 100%;
    left: 50%;
    margin-top: 10px;
    margin-left: -100px;
}


/* Here we are styling for class as left */

.left {
    right: 100%;
    top: -55%;
    margin-right: 7px;
}


/* Here we are styling for class as right */

.right {
    left: 100%;
    top: -50%;
    margin-left: 7px;
}


/* The style work done below is for the arrow according to the position i.e class assigned */

.bottom::after {
    content: " ";
    position: absolute;
    bottom: 100%;
    /* Top of the tooltip */
    left: 50%;
    margin-left: -10px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent black transparent;
}

.left::after {
    content: " ";
    position: absolute;
    top: 50%;
    left: 100%;
    /* Right of the tooltip */
    margin-top: -10px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent transparent black;
}

.right::after {
    content: " ";
    position: absolute;
    top: 50%;
    right: 100%;
    /* Left of the tooltip */
    margin-top: -10px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent black transparent transparent;
}

.top::after {
    content: " ";
    position: absolute;
    top: 100%;
    /*  Bottom of the tooltip */
    left: 50%;
    margin-left: -10px;
    border-width: 10px;
    border-style: solid;
    border-color: black transparent transparent transparent;
}